.title{
    text-align: center;
    margin:25px;
}



.photos img {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    box-shadow: inset 0 0 1px #e0e0e0, inset 0 0 14px rgba(0,0,0,0.2);
}

@media screen and (max-width: 600px) and (min-width: 320px) {
    .photos img {
        width: 50px;
        height: 50px;
    }
}

@media screen and (max-width: 800px) and (min-width: 600px) {
    .photos img {
        width: 70px;
        height: 70px;
    }
}

.photos,
.details {
    border-color: #e0e0e0;
    border-style: solid;
}

.photos {
    border-width: 1px 0px 0px 0px;
    text-align: center;
}

.details {
    border-width: 1px 0px 0px 0px;
    padding-left: 18px;
}

.e-bigger .details {
    padding-left: 25px;
}

.e-device .details {
    padding-left: 8px;
}

.details > table {
    width: 100%;
}

.CardHeader {
    font-weight: 600;
}

.templateRow td {
    padding: 2px 2px 3px 4px;
}

.image img {
    height: 55px;
    width: 55px;
    border-radius: 50px;
    box-shadow: inset 0 0 1px #e0e0e0, inset 0 0 14px rgba(0,0,0,0.2);
}
.detailtable td{
    font-size: 13px;
    padding: 4px;                      
    max-width: 0;
    /* overflow: hidden; */
    /* text-overflow: ellipsis; */
    white-space: nowrap;
}
.images{
    text-align: center;
}
.photo {     
    width: 100px;
    height: 100px;
    border-radius: 50px;
    box-shadow: inset 0 0 1px #e0e0e0, inset 0 0 14px rgba(0,0,0,0.2);
}

@media screen and (max-width: 800px) and (min-width: 320px) {
    .photo {
        width: 70px;
        height: 70px;
    }       
}

.e-ghidden {
opacity: 0.35;
}

.e-statustext {
font-size: 15px;
padding: 1em;
font-family: Roboto;
opacity: 0.87;
margin-top:25px;
} 

.e-dsalign {
margin-top: 10px;
float: right;
}

.select-wrap {
padding: 0 0 10px 0;
font-family: Roboto;
padding: 1em;
}

.select-wrap select {
height: 28px;
width: 100%;
border-width: 0 0 2px 0;
background: transparent;
}

.select-wrap select:focus {
border-bottom-style: solid;
border-color: #ff4081;
}

.select-wrap select, 
.select-wrap select option {
outline: none;
font-size: 14px;
padding: .2em;
height: 30px;
}

@font-face {
font-family: 'Material_copy';
src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype');
font-weight: normal;
font-style: normal;
}

.e-grid .e-copy::before {
content: "\e721";
font-family: 'Material_copy';
}

@media (min-width: 480px) and (max-width:639px) {
.select-wrap select {
    width: 40%
}
}

@media (min-width:640px) {
.select-wrap select {
    width: 25%
}
}

